@import 'vars';
@import 'mixins';
@import 'parts/datepicker';
@import 'popups/add-adress-popup';

.custom-radio-button [type=radio]:checked+label, .custom-radio-button [type=radio]:not(:checked)+label {
    padding-left: 22px;
}
.custom-form__delivery-method {
    .custom-radio-button [type=radio]:checked+label, .custom-radio-button [type=radio]:not(:checked)+label {
        span {
            font-weight: 400;
        }
    }
}

.blue-btn--cart {
    height: 42px;
    line-height: 42px;
}
.hint-link {
    color: #a6a6a6;
    font-size: 14px;
    font-weight: 400;
}
.add-greet {
    position: relative;
    max-width: 190px;
    min-width: 180px;
    img {
        display: inline-block;
        vertical-align: middle;
        padding-right: 10px;
    }
}
.chosen-prod-title {
    color: map_get($gray, default);
    // font-family: $frank-font;
    font-size: 14px;
    font-weight: 700;
    text-align: left;
}
.chosen-prod-label {
    color: #a6a6a6;
    font-size: 12px;
    font-weight: 400;
}
.chosen-prod-char {
    color: #a6a6a6;
    font-size: 14px;
    font-weight: 700;
}

.tabs--cart__controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #f7f7f7;
    .tabs__controls {
        background-color: #fff;
        padding-bottom: 5px;
    }
}

.tabs__list {
    padding: 0;
    padding-top: 15px;
}

// --------------------------- CART TAB ----------------------

ul.subtotal {
    margin: 0;
}
.subtotal {
    &__item {
        display: inline-block;
        margin-right: 10px;
        &--subtotal, &--grandtotal {
            text-transform: uppercase;
        }
    }
    &__text {
        font-size: 12px;
        text-transform: none;
        &--grandtotal {
            text-transform: uppercase;
        }
    }
    &__figure {
        color: #2b2b2b;
        font-size: 12px;
        // text-transform: uppercase;
        font-weight: 900;
    }
}

.cart-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    background-color: #fff;
    padding: 10px 10px 20px 20px;
    &__info {
        max-width: 560px;
        color: #2b2b2b;
        font-size: 14px;
        font-style: italic;
        line-height: 16px;
        padding-top: 32px;
    }
    .hint-title {
        color: #a6a6a6;
        font-size: 14px;
        font-weight: 400;
        margin-top: 5px;
    }
}

.delivery-opt {
    width: 42%;
    &__title {
        color: map_get($gray-light, default);
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase;
        margin-bottom: 15px;
    }
    
}

.delivery-method-form {
    padding-left: 10px;
    .custom-radio-button {
        display: block;
        margin-bottom: 5px;
    }
}

.cart-content {
    background-color: #fff;
    &.flex {
        display: flex;
    }
    &__end {
        width: 44%;
        margin-left: 2px;
        display: none;
        &.active {
            display: block;
        }
        .delivery-method-tab-content {
            &__item {
                display: none;
                .delivery-table-wrap {
                    height: auto;
                    max-height: 100%;
                }
                .delivery-table__tr td {
                    padding: 6px 0 6px 15px;
                }
                &.active {
                    display: block;
                }
                &[data-tab-cont='delivery-method-one'] {
                    .add-adress {
                        border-bottom: none;
                    }
                }
                &[data-tab-cont='delivery-method-pickup'] {
                    .add-adress {
                        border-bottom: none;
                    }
                    .delivery-table-wrap {
                        border-bottom: none;
                    }
                }
            }
        }
    }
}

.cart-row {
    background: #fff;
    display: flex;
    align-items: flex-start;
    padding-bottom: 30px;
    &__end {
        // width: 44%;
        width: 530px;
        display: none;
        &.active {
            display: block;
        }
    }
}
.set {
    height: 232px;
    background-color: #fff;
    @include clearfix;
    &__left {
        width: 140px;
        height: 100%;
        float: left;
        border-bottom: 1px solid #ececec;
    }
    &__right {
        width: 530px;
        height: 100%;
        float: left;
        position: relative;
        border: 1px solid #ececec;
        border-top: none;
    }

    &__title {
        height: 40px;
        line-height: 40px;
        background-color: #f3f3f3;
        color: map_get($gray-light, default);
        // font-family: $merri-font;
        font-size: 14px;
        font-weight: 700;
        padding-left: 10px;
        padding-right: 40px;
        white-space: nowrap; 
        overflow: hidden;
        text-overflow: ellipsis; 
    }
    &__title-adress {
        height: 40px;
        line-height: 40px;
        background-color: #f3f3f3;
        color: #a6a6a6;
        font-size: 14px;
        font-weight: 400;
        padding-left: 35px;
        padding-right: 40px;
        // text-align: justify;
        // &:after {
        //     @include justify; 
        // }
        display: flex;
        justify-content: space-between;
    }
    &__pic {
        display: block;
        width: 105px;
        max-height: 105px;
        margin: 20px auto 0 auto;
        img {
            display: block;
            max-width: 100%;
            height: auto;
            object-fit: cover;
        }
    }
}

.set-table {
    border-spacing: 0;
    border-collapse: collapse;
    margin-top: 30px;
    td {
        padding-left: 10px;
        padding-right: 10px;
        // vertical-align: middle;
        vertical-align: top;
        border-right: 1px solid #ececec;
        &:first-child {
            padding-left: 10px;
        }
        &:last-child {
            border-right: none;
        }
    }
    &__td-title {
        color: #a6a6a6;
        font-size: 14px;
        font-weight: 400;
        line-height: 12px;
        padding-bottom: 30px;
        
    }
    &__td-value {
        color: #a6a6a6;
        font-size: 14px;
        font-weight: 700;
        &--quant {
            width: 96px;
        }
    }
}

.set-options {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    border-top: 1px solid #ececec;
    
    &__item {
        display: inline-block;
        list-style: none;
        &:first-child {
            .set-options__link {
                padding-left: 10px;
            }
        }
    }
    &__link {
        display: inline-block;
        color: map_get($blue, default);
        font-size: 12px;
        font-weight: 700;
        padding: 10.5px 15px 10.5px 0;
        &:hover {
            color: map_get($blue, hover);
        }
        &:hover {
            color: map_get($blue, active);
        }
    }
}
ul.set-options__list {
    margin: 0;
    // display: flex;
    // justify-content: space-between;
}

.adress-specified {
    display: inline-block;
    &__number {
        &-first {
            color: $red-sale;
        }
        &-second {
            color: map_get($gray, default);
        }
    }
}

.adress-specified--all {
    .adress-specified__number-first, .adress-specified__number-second {
        color: map_get($green, default);
    }
}

.pick-delivery-adress {
    position: relative;
    margin-left: 15px;
    &::after {
        display: block;
        position: absolute;
        content: '';
        width: 160px;
        height: 1px;
        background-color: map_get($gray, default);
        bottom: -1px;
        left: 0;
    }
    &__input {
        width: 90%;
        
        padding: 5px 15px 5px 25px;
        outline: none;
        border: none;
        color: map_get($gray, default);
        font-size: 11px;
        font-weight: 400;
        line-height: 12px;
        background-image: url(../img/icon/search.png);
        background-repeat: no-repeat;
        background-position: 0% center;
        background-size: 13px 13px;
    }
}

.delivery-table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    &-wrap {
        height: 135px;
        // overflow-y: scroll;
        border-top: 1px solid #ececec;
        border-bottom: 1px solid #ececec;
    }
    &__tr {
        td {
            background-color: #fff;
            padding-left: 15px;
            // padding: 6px 0 6px 15px;
            min-height: 32px;
        }
        td.delivery-variant {
            width: 405px;
        }
        &:nth-child(even) {
            td {
                background-color: #f3f3f3;
            }
        }
    }
    .quant-conrol {
        background-color: transparent;
        .qty {
            background-color: transparent;
        }
        .qty-button {
            &::before {
                display: none;
            }
        }
    }
    .custom-radio-button [type=radio]:checked+label, .custom-radio-button [type=radio]:not(:checked)+label {
        font-weight: 400;
        color: map_get($gray-light, default);
        span {
            color: map_get($gray, default);
            font-weight: 700;
        }
    }

    .filter__checkbox+label span {
        font-weight: 700;
    }
}

.add-adress {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 30px 5px 15px;
    border-bottom: 1px solid #ececec;
    &__link {
        transition: .3s;
        display: inline-block;
        color: map_get($green, default);
        font-size: 14px;
        font-weight: 700;
        &:hover {
            color: map_get($green, hover);
        }
        &:hover {
            color: map_get($green, active);
        }
    }
    &__text {
        max-width: 345px;
        color: #a6a6a6;
        font-size: 11px;
        font-style: italic;
        line-height: 12px;
    }
}

//  custom-checkbox
.filter__checkbox {
    &-row {
        position: relative;
    }
    &+label {
        color: map_get($gray-light, default);
        font-size: 14px;
        font-weight: 400;
        line-height: 16px;
        cursor: pointer;
        // max-width: 185px;
        display: inline-block;
        span {
            color: map_get($gray, default);
        }
    }
}

.btn-block {
    text-align: center;
    background-color: #fff;
    padding-bottom: 60px;
    .compliment-text {
        color: #2b2b2b;
        font-size: 14px;
        font-style: italic;
        line-height: 20px;
        margin-bottom: 35px;
    }
}



// --------------------------- DELIVERY TAB ----------------------
.package {
    &__title {
        color: map_get($gray, default);
        font-size: 18px;
        font-weight: 900;
        text-align: center;
        padding: 17px 0 15px;
        background-color: #f7f7f7;
        .package__destination {
            color: map_get($gray-light, default);
            text-transform: uppercase;
        }
    }
    &__content {
        background-color: #fff;
        padding-bottom: 30px;
    }
}

.package-table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    thead {
        td {
            width: 90px;
            color: #a6a6a6;
            font-size: 14px;
            font-weight: 400;
            line-height: 14px;
            border-bottom: none;
            // padding-top: 10px;
            padding: 10px 5px;
        }
    }
    td {
        border: 1px solid #ececec;
        text-align: center;
    }
    &__tr {
        &--overall {
            td {
                border-top: none;
                padding-bottom: 15px;
                span {
                    color: #2b2b2b;
                    font-size: 18px;
                    font-weight: 700;
                }
            }
        }
        &--content {
            td {
                padding: 10px 10px 10px 10px;
                border-bottom: 2px solid #a6a6a6;
            }
            .chosen-prod {
                text-align: left;
                
                &__list {
                    margin-left: 0;
                    margin-bottom: 0;
                    text-align: left;
                }
                &__item {
                    display: flex;
                    list-style: none;
                    margin-bottom: 10px;
                    border-bottom: 1px solid #ececec;
                    padding-bottom: 10px;
                    &:last-child {
                        padding-bottom: 0;
                        margin-bottom: 0;
                        border-bottom: none;
                    }
                }
                &__item-pic {
                    // width: 72px;
                    // height: 55px;
                    max-width: 90px;
                    max-height: 90px;
                    display: block;
                    img {
                        max-width: 100%;
                        height: auto;
                        display: block;
                        object-fit: cover;
                    }
                }
                &__start {

                }
                &__end {
                    margin-left: 20px;
                    width: 100%;
                }
                &__settings {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                }
            }
        }
    }
    td.adress {
        width: 160px;
        padding: 10px 30px;
        text-align: left;
        vertical-align: top;
    }
    .chosen-prod-title {
        margin-bottom: 5px;
    }
}

.settings__col {
    .custom-radio-button:first-child {
        margin-right: 5px;
    }
    &--quant {
        display: flex;
        align-items: center;
        .quant-conrol {
            margin-left: 3px;
        }
        .chosen-prod-label {
            padding-top: 2px;
        }
    }
    &--flavor {
        max-width: 175px;
    }
    &--radio {
        max-width: 325px;
    }
}

section.payment {
    background-color: #fff;
    padding-bottom: 30px;
    .payment__container {
        padding-left: 320px;
        .blue-btn--place-order {
            margin-top: 20px;
        }
    }
}

.payment-opt {
    padding-left: 15px;
    &__title {
        color: map_get($gray-light, default);
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase;
        margin-bottom: 15px;
    }
    &__radio-group {
        padding-left: 15px;
        .custom-radio-button {
            display: block;
            margin-bottom: 10px;
            &:last-child {
                margin-bottom: 0;
            }
        }
        margin-bottom: 25px;
    }
}

.promo-code {
    margin-bottom: 25px;
    &__title {
        color: #2b2b2b;
        font-size: 16px;
        font-weight: 400;
        margin-bottom: 20px;
        padding-left: 15px;
    }
    &__input {
        display: inline-block;
        vertical-align: middle;
        border-radius: 3px;
        border: 1px solid #a6a6a6;
        background-color: #ffffff;
        color: #3c3535;
        font-size: 16px;
        font-weight: 400;
        text-transform: uppercase;
        outline: none;
        width: 214px;
        padding: 9px 15px 9px 15px;
    }
    .promo-code__input-wrap {
        display: inline-block;
    }
    &__input-pic {
        width: 20px;
        height: 22px;
        display: inline-block;
        vertical-align: middle;
        margin-right: 10px;
    }
}

.order-sum {
    padding-left: 30px;
    td {
        vertical-align: top;
    }
    &__row {
    }
    &__title {
        color: #a6a6a6;
        font-size: 14px;
        font-weight: 400;
        width: 120px;
        padding-bottom: 5px;
        &--grandtotal {
            text-transform: uppercase;
        }
    }
    &__money {
        color: #2b2b2b;
        font-size: 14px;
        font-weight: 700;
        text-transform: uppercase;
        padding-left: 45px;
    }
}

.h-gray-line {
    height: 2px;
    width: 100%;
    background-color: #a6a6a6;
    margin-top: 20px;
    margin-bottom: 15px;
}

.pay-card {
    width: 432px;
    border-radius: 3px;
    background-color: #f7f7f7;
    margin-bottom: 30px;
    padding: 20px 0 20px 15px;
    .btn-block {
        text-align: right;
        background-color: #f7f7f7;
    }
    .btn--green {
        width: 150px;
        height: 38px;
        line-height: 38px;
    }
    &__title {
        color: map_get($gray, default);
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase;
        margin-bottom: 30px;
    }
    &__content {
        padding: 0 30px 0 15px;
    }
    &__card-block {
        border-bottom: 1px solid map_get($gray-light, default);
    }
    &__credentials {
        margin-top: 25px;
    }
    &__input {
        border: none;
        outline: none;
        padding: 0px 15px 5px;
        color: map_get($gray, default);
        background-color: transparent;
        border-bottom: 1px solid map_get($gray, hover);
        &:focus {
            border-bottom: 1px solid map_get($gray-light, active);
        }
    }
    &__input-row {
        margin-bottom: 20px;
        &--card-numbers {
            display: flex;
            justify-content: space-between;
            .pay-card__input {
                width: 68px;
                text-align: center;
                padding: 0px 10px 5px;
            }
        }
        &--exp-date {
            display: flex;
            justify-content: space-between;
            margin-bottom: 30px;
            .pay-card__input {
                width: 55px;
                text-align: center;
                padding: 0px 5px 5px;
                // border-bottom: 1px solid map_get($gray-light, default);
                // &:focus {
                //     border-bottom: 1px solid map_get($gray-light, active);
                // }
            }
            .divider {
                color: #a6a6a6;
                font-size: 16px;
                font-weight: 400;
            }
        }
        &--name {
            display: flex;
            justify-content: space-between;
            .pay-card__input {
                width: 115px;
                // border-bottom: 1px solid map_get($brown, default);
            }
        }
        &--adress {
            .pay-card__input {
                width: 100%;
            }
        }
        &--city {
            display: flex;
            justify-content: space-between;
            .pay-card__input {
                width: 114px;
            }
        }
    }
    
}


// Specific styles for Mozilla.
@-moz-document url-prefix() {
    // Выровнять бордеры по высоте
    .delivery-table-wrap {
        height: 134px;
    }
}